/**
 * API 
 *  HTTP  SSE 
 */

import {
  ChatRequest,
  SessionListResponse,
  HistoryResponse,
  FileUploadResponse,
  SSEEvent,
  ApiResponse
} from '../types/chat';

/**
 * API 
 */
const API_BASE_URL = 'http://localhost:9000';

/**
 * HTTP 
 */
class HttpClient {
  private baseUrl: string;

  constructor(baseUrl: string = API_BASE_URL) {
    this.baseUrl = baseUrl || 'http://localhost:9000';
  }

  /**
   *  GET 
   */
  async get<T>(endpoint: string, params?: Record<string, string>): Promise<T> {
    //  baseUrl 
    const baseUrl = this.baseUrl || 'http://localhost:9000';
    const url = new URL(endpoint, baseUrl);
    
    if (params) {
      Object.entries(params).forEach(([key, value]) => {
        url.searchParams.append(key, value);
      });
    }

    const response = await fetch(url.toString(), {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
      },
    });

    if (!response.ok) {
      throw new Error(`HTTP error! status: ${response.status}`);
    }

    return response.json();
  }

  /**
   *  POST 
   */
  async post<T>(endpoint: string, data?: any): Promise<T> {
    const baseUrl = this.baseUrl || 'http://localhost:9000';
    const url = new URL(endpoint, baseUrl);
    
    const response = await fetch(url.toString(), {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: data ? JSON.stringify(data) : undefined,
    });

    if (!response.ok) {
      throw new Error(`HTTP error! status: ${response.status}`);
    }

    return response.json();
  }

  /**
   * 
   */
  async uploadFiles(files: FileList): Promise<FileUploadResponse> {
    const baseUrl = this.baseUrl || 'http://localhost:9000';
    const url = new URL('/upload', baseUrl);
    const formData = new FormData();
    
    Array.from(files).forEach((file) => {
      formData.append('files', file);
    });

    const response = await fetch(url.toString(), {
      method: 'POST',
      body: formData,
    });

    if (!response.ok) {
      throw new Error(`Upload failed! status: ${response.status}`);
    }

    return response.json();
  }
}

/**
 * SSE 
 */
export class SSEClient {
  private eventSource: EventSource | null = null;
  private url: string;

  constructor(endpoint: string, baseUrl: string = API_BASE_URL) {
    this.url = new URL(endpoint, baseUrl).toString();
  }

  /**
   *  SSE 
   */
  connect(
    onMessage: (event: SSEEvent) => void,
    onError: (error: Error) => void,
    onComplete: () => void
  ): void {
    this.eventSource = new EventSource(this.url);

    this.eventSource.onmessage = (event) => {
      try {
        const data: SSEEvent = JSON.parse(event.data);
        
        if (data.type === 'done') {
          onComplete();
          this.close();
        } else if (data.type === 'error') {
          onError(new Error(data.error || 'Unknown error'));
          this.close();
        } else {
          onMessage(data);
        }
      } catch (err) {
        onError(new Error('Failed to parse SSE message'));
      }
    };

    this.eventSource.onerror = () => {
      onError(new Error('SSE connection error'));
      this.close();
    };
  }

  /**
   *  SSE 
   */
  close(): void {
    if (this.eventSource) {
      this.eventSource.close();
      this.eventSource = null;
    }
  }
}

/**
 *  API 
 */
export class ChatApiService {
  private httpClient: HttpClient;

  constructor() {
    this.httpClient = new HttpClient();
  }

  /**
   * 
   */
  async healthCheck(): Promise<{ status: string }> {
    return this.httpClient.get('/health');
  }

  /**
   * 
   */
  async getSessions(userId: string): Promise<SessionListResponse> {
    return this.httpClient.get('/sessions', { user_id: userId });
  }

  /**
   * 
   */
  async getHistory(userId: string, sessionId: string): Promise<HistoryResponse> {
    return this.httpClient.get('/history', { 
      user_id: userId, 
      session_id: sessionId 
    });
  }

  /**
   * 
   */
  async startStreamingChat(
    request: ChatRequest,
    onMessage: (event: SSEEvent) => void,
    onError: (error: Error) => void,
    onComplete: () => void
  ): Promise<SSEClient> {
    console.log(' :', request);
    
    try {
      const baseUrl = API_BASE_URL || 'http://localhost:9000';
      const url = new URL('/chat/stream', baseUrl).toString();
      console.log(' URL:', url);
      
      const response = await fetch(url, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(request),
      });

      console.log(' :', response.status, response.statusText);

      if (!response.ok) {
        const errorText = await response.text();
        console.error(' HTTP :', response.status, errorText);
        throw new Error(`HTTP error! status: ${response.status}, message: ${errorText}`);
      }

      //  SSE 
      const reader = response.body?.getReader();
      if (!reader) {
        throw new Error('Response body is not readable');
      }

      const decoder = new TextDecoder();
      const sseClient = new SSEClient('');

      //  SSE 
      const processStream = async () => {
        let buffer = '';
        console.log(' SSE...');
        
        try {
          while (true) {
            const { done, value } = await reader.read();
            if (done) {
              console.log(' ');
              break;
            }

            const chunk = decoder.decode(value, { stream: true });
            buffer += chunk;
            console.log(' :', chunk);

            // 
            const lines = buffer.split('\n');
            buffer = lines.pop() || ''; // 

            for (const line of lines) {
              if (line.startsWith('data: ')) {
                try {
                  const jsonData = line.slice(6).trim();
                  if (jsonData) {
                    const data = JSON.parse(jsonData);
                    console.log(' SSE:', data);
                    
                    if (data.type === 'done') {
                      console.log(' ');
                      onComplete();
                      return;
                    } else if (data.type === 'error') {
                      console.error(' :', data.error);
                      onError(new Error(data.error || 'Unknown error'));
                      return;
                    } else {
                      onMessage(data);
                    }
                  }
                } catch (err) {
                  console.warn(' SSE:', line, err);
                }
              }
            }
          }
          
          //  buffer
          if (buffer.trim()) {
            console.log(' :', buffer);
          }
          
        } catch (error) {
          console.error(' :', error);
          onError(error instanceof Error ? error : new Error('Stream error'));
        } finally {
          reader.releaseLock();
          console.log(' ');
        }
      };

      // 
      processStream().catch(error => {
        console.error(' :', error);
        onError(error instanceof Error ? error : new Error('Stream processing failed'));
      });
      
      return sseClient;
    } catch (error) {
      console.error(' :', error);
      onError(error instanceof Error ? error : new Error('Failed to start chat'));
      throw error;
    }
  }

  /**
   * 
   */
  async uploadFiles(files: FileList): Promise<FileUploadResponse> {
    return this.httpClient.uploadFiles(files);
  }
}

/**
 * 
 */
export const chatApiService = new ChatApiService();

/**
 * 
 */
export class ApiError extends Error {
  constructor(
    message: string,
    public status?: number,
    public code?: string
  ) {
    super(message);
    this.name = 'ApiError';
  }
}

/**
 * API 
 */
export const apiResponse = {
  success: <T>(data: T): ApiResponse<T> => ({
    success: true,
    data,
  }),
  
  error: (message: string, code?: string): ApiResponse => ({
    success: false,
    error: message,
  }),
};
