/**
 * 
 * ChatGPT
 */

import React, { useState } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { 
  X, 
  Settings, 
  Menu, 
  Plus, 
  MessageSquare, 
  Moon, 
  Sun, 
  Monitor,
  Search,
  Edit3,
  Trash2,
  Clock
} from 'lucide-react';
import { ChatSession } from '../../types/chat';
import { cn } from '../../utils/cn';
import { Button } from '../ui/Button';
import { useTheme } from '../../contexts/ThemeContext';

/**
 * 
 */
interface SidebarProps {
  sessions: ChatSession[];
  currentSessionId: string | null;
  onSessionSelect: (sessionId: string | null) => void;
  onNewSession: () => void;
  isOpen: boolean;
  onToggle: () => void;
  isLoading?: boolean;
  className?: string;
}

/**
 * 
 */
function ThemeToggle() {
  const { theme, setTheme } = useTheme();

  const themes = [
    { value: 'light', icon: Sun, label: '' },
    { value: 'dark', icon: Moon, label: '' },
    { value: 'system', icon: Monitor, label: '' },
  ] as const;

  return (
    <div className="flex items-center gap-1 p-1 bg-muted rounded-lg">
      {themes.map(({ value, icon: Icon, label }) => (
        <Button
          key={value}
          variant={theme === value ? "default" : "ghost"}
          size="sm"
          onClick={() => setTheme(value)}
          className={cn(
            "h-8 w-8 p-0",
            theme === value && "bg-primary text-primary-foreground shadow-sm"
          )}
          title={label}
        >
          <Icon className="h-4 w-4" />
        </Button>
      ))}
    </div>
  );
}

/**
 * 
 */
function SessionItem({ 
  session, 
  isActive, 
  onSelect,
  onDelete,
  onRename 
}: { 
  session: ChatSession;
  isActive: boolean;
  onSelect: () => void;
  onDelete?: () => void;
  onRename?: () => void;
}) {
  const [showActions, setShowActions] = useState(false);

  return (
    <motion.div
      layout
      initial={{ opacity: 0, y: 10 }}
      animate={{ opacity: 1, y: 0 }}
      exit={{ opacity: 0, y: -10 }}
      className="group"
      onMouseEnter={() => setShowActions(true)}
      onMouseLeave={() => setShowActions(false)}
    >
      <div
        onClick={onSelect}
        className={cn(
          "flex items-center gap-3 p-3 rounded-lg cursor-pointer transition-all duration-200",
          "hover:bg-sidebar-item-hover",
          isActive 
            ? "bg-primary text-primary-foreground shadow-sm" 
            : "text-foreground"
        )}
      >
        <MessageSquare className="h-4 w-4 flex-shrink-0" />
        
        <div className="flex-1 min-w-0">
          <div className="font-medium text-sm truncate" title={session.title}>
            {session.title}
          </div>
          <div className="flex items-center gap-2 mt-1">
            <span className="text-xs opacity-70">
              {new Date(session.updatedAt).toLocaleString('zh-CN', {
                month: '2-digit',
                day: '2-digit',
                hour: '2-digit',
                minute: '2-digit'
              })}
            </span>
            <span className="text-xs opacity-70">
              {session.messageCount} 
            </span>
          </div>
        </div>

        {/*  */}
        <AnimatePresence>
          {showActions && !isActive && (
            <motion.div
              initial={{ opacity: 0, scale: 0.8 }}
              animate={{ opacity: 1, scale: 1 }}
              exit={{ opacity: 0, scale: 0.8 }}
              className="flex items-center gap-1"
              onClick={(e) => e.stopPropagation()}
            >
              <Button
                variant="ghost"
                size="sm"
                onClick={onRename}
                className="h-6 w-6 p-0 opacity-70 hover:opacity-100"
              >
                <Edit3 className="h-3 w-3" />
              </Button>
              <Button
                variant="ghost"
                size="sm"
                onClick={onDelete}
                className="h-6 w-6 p-0 opacity-70 hover:opacity-100 hover:text-destructive"
              >
                <Trash2 className="h-3 w-3" />
              </Button>
            </motion.div>
          )}
        </AnimatePresence>

        {isActive && (
          <div className="h-2 w-2 bg-primary-foreground rounded-full" />
        )}
      </div>
    </motion.div>
  );
}

/**
 * 
 */
function SearchBox({ 
  value, 
  onChange, 
  placeholder = "..." 
}: {
  value: string;
  onChange: (value: string) => void;
  placeholder?: string;
}) {
  return (
    <div className="relative">
      <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 h-4 w-4 text-muted-foreground" />
      <input
        type="text"
        value={value}
        onChange={(e) => onChange(e.target.value)}
        placeholder={placeholder}
        className="w-full pl-10 pr-4 py-2 bg-background border border-border rounded-lg text-sm focus:outline-none focus:ring-2 focus:ring-primary/20"
      />
    </div>
  );
}

/**
 * 
 */
function NewChatButton({ onClick, disabled }: { onClick: () => void; disabled?: boolean }) {
  return (
    <motion.div
      whileHover={{ scale: 1.02 }}
      whileTap={{ scale: 0.98 }}
    >
      <Button
        onClick={onClick}
        disabled={disabled}
        className={cn(
          "w-full h-12 bg-primary hover:bg-primary/90 text-primary-foreground",
          "shadow-sm hover:shadow-md transition-all duration-200",
          "font-medium text-sm flex items-center gap-3 justify-center"
        )}
      >
        <Plus className="h-4 w-4" />
        
      </Button>
    </motion.div>
  );
}

/**
 * 
 */
export function NewSidebar({
  sessions,
  currentSessionId,
  onSessionSelect,
  onNewSession,
  isOpen,
  onToggle,
  isLoading = false,
  className,
}: SidebarProps) {
  const [searchQuery, setSearchQuery] = useState('');

  // 
  const filteredSessions = React.useMemo(() => {
    return sessions
      .filter(session =>
        session.title.toLowerCase().includes(searchQuery.toLowerCase()) ||
        session.preview?.toLowerCase().includes(searchQuery.toLowerCase())
      )
      // 
      .sort((a, b) => {
        const timeA = typeof a.updatedAt === 'number' ? a.updatedAt : new Date(a.updatedAt).getTime();
        const timeB = typeof b.updatedAt === 'number' ? b.updatedAt : new Date(b.updatedAt).getTime();
        return timeB - timeA; // 
      });
  }, [sessions, searchQuery]);

  // 
  const groupedSessions = React.useMemo(() => {
    const groups: { [key: string]: ChatSession[] } = {};
    
    filteredSessions.forEach(session => {
      const date = new Date(session.updatedAt);
      const today = new Date();
      const yesterday = new Date(today);
      yesterday.setDate(yesterday.getDate() - 1);
      
      let groupKey = '';
      if (date.toDateString() === today.toDateString()) {
        groupKey = '';
      } else if (date.toDateString() === yesterday.toDateString()) {
        groupKey = '';
      } else if (date.getTime() > today.getTime() - 7 * 24 * 60 * 60 * 1000) {
        groupKey = ' 7 ';
      } else {
        groupKey = '';
      }
      
      if (!groups[groupKey]) {
        groups[groupKey] = [];
      }
      groups[groupKey].push(session);
    });
    
    // 
    Object.keys(groups).forEach(groupKey => {
      groups[groupKey].sort((a, b) => {
        const timeA = typeof a.updatedAt === 'number' ? a.updatedAt : new Date(a.updatedAt).getTime();
        const timeB = typeof b.updatedAt === 'number' ? b.updatedAt : new Date(b.updatedAt).getTime();
        return timeB - timeA; // 
      });
    });
    
    return groups;
  }, [filteredSessions]);

  const handleSessionDelete = (sessionId: string) => {
    // TODO: 
    console.log('Delete session:', sessionId);
  };

  const handleSessionRename = (sessionId: string) => {
    // TODO: 
    console.log('Rename session:', sessionId);
  };

  return (
    <>
      {/*  - Claude  */}
      <AnimatePresence>
        {isOpen && (
          <motion.div
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            exit={{ opacity: 0 }}
            className="fixed inset-0 bg-black/20 dark:bg-black/50 z-40"
            onClick={onToggle}
          />
        )}
      </AnimatePresence>

      {/*  */}
      <AnimatePresence>
        {isOpen && (
          <motion.div
            initial={{ x: -320, opacity: 0 }}
            animate={{ x: 0, opacity: 1 }}
            exit={{ x: -320, opacity: 0 }}
            transition={{ type: "spring", damping: 30, stiffness: 300 }}
            className={cn(
              'fixed left-0 top-0 h-full w-80 bg-sidebar border-r z-50 shadow-xl',
              className
            )}
          >
        <div className="flex flex-col h-full">
          {/*  */}
          <div className="p-4 border-b">
            <div className="flex items-center justify-between mb-4">
              <motion.h1 
                className="font-bold text-xl bg-gradient-to-r from-primary to-primary/80 bg-clip-text text-transparent"
                initial={{ opacity: 0, y: -10 }}
                animate={{ opacity: 1, y: 0 }}
              >
                MatterAI
              </motion.h1>
              
              {/*  - Claude  */}
              <Button
                variant="ghost"
                size="icon"
                onClick={onToggle}
                className="h-8 w-8"
                title=""
              >
                <X className="h-4 w-4" />
              </Button>
            </div>

            {/*  */}
            <NewChatButton onClick={onNewSession} disabled={isLoading} />
          </div>

          {/*  */}
          <div className="p-4 border-b">
            <SearchBox
              value={searchQuery}
              onChange={setSearchQuery}
              placeholder="..."
            />
          </div>

          {/*  */}
          <div className="flex-1 overflow-y-auto scrollbar-thin p-4">
            {isLoading && sessions.length === 0 ? (
              <div className="flex items-center justify-center py-8">
                <div className="flex items-center gap-2 text-muted-foreground">
                  <div className="w-4 h-4 border-2 border-current border-t-transparent rounded-full animate-spin" />
                  <span className="text-sm">...</span>
                </div>
              </div>
            ) : Object.keys(groupedSessions).length === 0 ? (
              <div className="text-center py-8">
                <MessageSquare className="h-12 w-12 mx-auto text-muted-foreground mb-3" />
                <p className="text-muted-foreground text-sm">
                  {searchQuery ? '' : ''}
                </p>
              </div>
            ) : (
              <div className="space-y-6">
                {Object.entries(groupedSessions)
                  //  >  > 7 > 
                  .sort(([a], [b]) => {
                    const order = ['', '', ' 7 ', ''];
                    return order.indexOf(a) - order.indexOf(b);
                  })
                  .map(([groupName, groupSessions]) => (
                  <motion.div
                    key={groupName}
                    initial={{ opacity: 0, y: 20 }}
                    animate={{ opacity: 1, y: 0 }}
                    className="space-y-2"
                  >
                    <div className="flex items-center gap-2 px-2 py-1">
                      <Clock className="h-3 w-3 text-muted-foreground" />
                      <h3 className="text-xs font-medium text-muted-foreground uppercase tracking-wider">
                        {groupName}
                      </h3>
                    </div>
                    
                    <div className="space-y-1">
                      {groupSessions.map((session) => (
                        <SessionItem
                          key={session.id}
                          session={session}
                          isActive={currentSessionId === session.id}
                          onSelect={() => onSessionSelect(session.id)}
                          onDelete={() => handleSessionDelete(session.id)}
                          onRename={() => handleSessionRename(session.id)}
                        />
                      ))}
                    </div>
                  </motion.div>
                ))}
              </div>
            )}
          </div>

          {/*  */}
          <div className="p-4 border-t">
            <div className="flex items-center justify-between">
              <div className="flex items-center gap-2">
                <Button
                  variant="ghost"
                  size="icon"
                  className="h-8 w-8"
                >
                  <Settings className="h-4 w-4" />
                </Button>
                <span className="text-sm text-muted-foreground"></span>
              </div>
              
              <ThemeToggle />
            </div>
          </div>
        </div>
          </motion.div>
        )}
      </AnimatePresence>

      {/*  -  */}
      <AnimatePresence>
        {!isOpen && (
          <motion.div
            initial={{ opacity: 0, scale: 0.8 }}
            animate={{ opacity: 1, scale: 1 }}
            exit={{ opacity: 0, scale: 0.8 }}
            className="fixed top-4 left-4 z-30"
          >
            <Button
              variant="default"
              size="icon"
              onClick={onToggle}
              className="h-10 w-10 shadow-lg"
              title=""
            >
              <Menu className="h-5 w-5" />
            </Button>
          </motion.div>
        )}
      </AnimatePresence>
    </>
  );
}
