/**
 * 
 * 
 */

import React, { useState, useRef, useCallback, KeyboardEvent } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { 
  Paperclip, 
  X, 
  Smile, 
  Mic, 
  Square,
  ArrowUp,
  Image as ImageIcon
} from 'lucide-react';
import { cn } from '../../utils/cn';
import { Button } from '../ui/Button';
import { FileUpload } from './FileUpload';
import { ToolSelector } from '../tools/ToolSelector';

/**
 * 
 */
interface ChatInputProps {
  onSendMessage: (message: string, files?: FileList, selectedTools?: string[]) => void;
  disabled?: boolean;
  placeholder?: string;
  className?: string;
  selectedTools?: string[];
  onToolsChange?: (tools: string[]) => void;
}

/**
 * 
 */
function FilePreview({ 
  files, 
  onRemove 
}: { 
  files: FileList;
  onRemove: (index: number) => void;
}) {
  return (
    <AnimatePresence>
      <motion.div
        initial={{ opacity: 0, height: 0 }}
        animate={{ opacity: 1, height: 'auto' }}
        exit={{ opacity: 0, height: 0 }}
        className="flex flex-wrap gap-2 p-3 bg-muted/50 border-t"
      >
        {Array.from(files).map((file, index) => (
          <motion.div
            key={`${file.name}-${index}`}
            initial={{ opacity: 0, scale: 0.8 }}
            animate={{ opacity: 1, scale: 1 }}
            exit={{ opacity: 0, scale: 0.8 }}
            className="flex items-center gap-2 px-3 py-2 bg-background border rounded-lg text-sm"
          >
            <ImageIcon className="h-4 w-4 text-muted-foreground" />
            <span className="truncate max-w-32">{file.name}</span>
            <Button
              variant="ghost"
              size="sm"
              onClick={() => onRemove(index)}
              className="h-5 w-5 p-0 hover:bg-destructive/10 hover:text-destructive"
            >
              <X className="h-3 w-3" />
            </Button>
          </motion.div>
        ))}
      </motion.div>
    </AnimatePresence>
  );
}

/**
 * 
 */
function QuickActions({ 
  onFileUpload, 
  onEmojiClick,
  onVoiceClick,
  disabled 
}: {
  onFileUpload: () => void;
  onEmojiClick?: () => void;
  onVoiceClick?: () => void;
  disabled?: boolean;
}) {
  return (
    <div className="flex items-center gap-1">
      <Button
        variant="ghost"
        size="icon"
        onClick={onFileUpload}
        disabled={disabled}
        className="h-8 w-8 text-muted-foreground hover:text-foreground"
      >
        <Paperclip className="h-4 w-4" />
      </Button>
      
      {onEmojiClick && (
        <Button
          variant="ghost"
          size="icon"
          onClick={onEmojiClick}
          disabled={disabled}
          className="h-8 w-8 text-muted-foreground hover:text-foreground"
        >
          <Smile className="h-4 w-4" />
        </Button>
      )}
      
      {onVoiceClick && (
        <Button
          variant="ghost"
          size="icon"
          onClick={onVoiceClick}
          disabled={disabled}
          className="h-8 w-8 text-muted-foreground hover:text-foreground"
        >
          <Mic className="h-4 w-4" />
        </Button>
      )}
    </div>
  );
}

/**
 * 
 */
function SendButton({ 
  canSend, 
  isLoading, 
  onClick 
}: { 
  canSend: boolean;
  isLoading?: boolean;
  onClick: () => void;
}) {
  return (
    <motion.div
      whileHover={canSend ? { scale: 1.05 } : {}}
      whileTap={canSend ? { scale: 0.95 } : {}}
    >
      <Button
        onClick={onClick}
        disabled={!canSend}
        size="icon"
        className={cn(
          "h-8 w-8 rounded-lg transition-all duration-200",
          canSend 
            ? "bg-primary hover:bg-primary/90 text-primary-foreground shadow-sm hover:shadow-md" 
            : "bg-muted text-muted-foreground cursor-not-allowed"
        )}
      >
        {isLoading ? (
          <motion.div
            animate={{ rotate: 360 }}
            transition={{ duration: 1, repeat: Infinity, ease: "linear" }}
          >
            <Square className="h-4 w-4" />
          </motion.div>
        ) : (
          <ArrowUp className="h-4 w-4" />
        )}
      </Button>
    </motion.div>
  );
}

/**
 * 
 */
export function NewChatInput({ 
  onSendMessage, 
  disabled = false, 
  placeholder = " MatterAI ...",
  className,
  selectedTools = [],
  onToolsChange
}: ChatInputProps) {
  const [message, setMessage] = useState('');
  const [files, setFiles] = useState<FileList | null>(null);
  const [showFileUpload, setShowFileUpload] = useState(false);
  const [isRecording, setIsRecording] = useState(false); // TODO: 
  const textareaRef = useRef<HTMLTextAreaElement>(null);
  const fileInputRef = useRef<HTMLInputElement>(null);

  /**
   * 
   */
  const adjustTextareaHeight = useCallback(() => {
    const textarea = textareaRef.current;
    if (textarea) {
      textarea.style.height = 'auto';
      const maxHeight = 120; // 
      const scrollHeight = Math.min(textarea.scrollHeight, maxHeight);
      textarea.style.height = `${scrollHeight}px`;
    }
  }, []);

  /**
   * 
   */
  const handleInputChange = useCallback((e: React.ChangeEvent<HTMLTextAreaElement>) => {
    setMessage(e.target.value);
    adjustTextareaHeight();
  }, [adjustTextareaHeight]);

  /**
   * 
   */
  const handleSend = useCallback(() => {
    const trimmedMessage = message.trim();
    
    // 
    if (!trimmedMessage && !files?.length) {
      return;
    }

    // 
    onSendMessage(trimmedMessage, files || undefined, selectedTools);

    // 
    setMessage('');
    setFiles(null);
    setShowFileUpload(false);

    // 
    if (textareaRef.current) {
      textareaRef.current.style.height = 'auto';
    }
  }, [message, files, selectedTools, onSendMessage]);

  /**
   * 
   */
  const handleKeyDown = useCallback((e: KeyboardEvent<HTMLTextAreaElement>) => {
    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault();
      handleSend();
    }
  }, [handleSend]);

  /**
   * 
   */
  const handleFileSelect = useCallback(() => {
    fileInputRef.current?.click();
  }, []);

  /**
   * 
   */
  const handleFilesChange = useCallback((e: React.ChangeEvent<HTMLInputElement>) => {
    const selectedFiles = e.target.files;
    if (selectedFiles && selectedFiles.length > 0) {
      setFiles(selectedFiles);
      setShowFileUpload(false);
    }
  }, []);

  /**
   * 
   */
  const handleRemoveFile = useCallback((index: number) => {
    if (files) {
      const fileArray = Array.from(files);
      fileArray.splice(index, 1);
      
      //  FileList
      const dt = new DataTransfer();
      fileArray.forEach(file => dt.items.add(file));
      setFiles(dt.files.length > 0 ? dt.files : null);
    }
  }, [files]);

  /**
   * 
   */
  const toggleFileUpload = useCallback(() => {
    setShowFileUpload(prev => !prev);
  }, []);

  /**
   * 
   */
  const handleVoiceClick = useCallback(() => {
    setIsRecording(prev => !prev);
    // TODO: 
  }, []);

  const canSend = (message.trim().length > 0 || (files?.length ?? 0) > 0) && !disabled;

  return (
    <motion.div
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      className={cn('border-t bg-background', className)}
    >
      {/*  */}
      <input
        ref={fileInputRef}
        type="file"
        multiple
        accept="image/*,application/pdf,text/*,.doc,.docx,.xls,.xlsx,.ppt,.pptx"
        onChange={handleFilesChange}
        className="hidden"
      />

      {/*  */}
      <AnimatePresence>
        {showFileUpload && (
          <motion.div
            initial={{ opacity: 0, height: 0 }}
            animate={{ opacity: 1, height: 'auto' }}
            exit={{ opacity: 0, height: 0 }}
            className="p-4 border-b bg-muted/30"
          >
            <div className="flex items-center justify-between mb-3">
              <h4 className="font-medium text-sm"></h4>
              <Button
                variant="ghost"
                size="icon"
                onClick={toggleFileUpload}
                className="h-6 w-6"
              >
                <X className="h-4 w-4" />
              </Button>
            </div>
            <FileUpload
              onFilesChange={(newFiles) => setFiles(newFiles)}
              acceptedTypes={[
                'image/*',
                'application/pdf',
                'text/*',
                '.doc',
                '.docx',
                '.xls',
                '.xlsx',
                '.ppt',
                '.pptx'
              ]}
              maxFiles={5}
              maxFileSize={10 * 1024 * 1024} // 10MB
              disabled={disabled}
            />
          </motion.div>
        )}
      </AnimatePresence>

      {/*  */}
      {onToolsChange && (
        <div className="px-4 pt-2 pb-1 bg-background/95 backdrop-blur-sm">
          <ToolSelector
            selectedTools={selectedTools}
            onToolsChange={onToolsChange}
          />
        </div>
      )}

      {/*  */}
      {files && files.length > 0 && (
        <FilePreview files={files} onRemove={handleRemoveFile} />
      )}

      {/*  */}
      <div className="px-4 pt-1 pb-4">
        <div
          className={cn(
            "flex items-end gap-3 p-3 rounded-2xl border border-border bg-background",
            "focus-within:border-primary/50 focus-within:ring-1 focus-within:ring-primary/20",
            "transition-all duration-200",
            disabled && "opacity-50"
          )}
        >
          {/*  */}
          <QuickActions
            onFileUpload={handleFileSelect}
            onVoiceClick={handleVoiceClick}
            disabled={disabled}
          />

          {/*  */}
          <div className="flex-1">
            <textarea
              ref={textareaRef}
              value={message}
              onChange={handleInputChange}
              onKeyDown={handleKeyDown}
              placeholder={placeholder}
              disabled={disabled}
              rows={1}
              className={cn(
                'w-full resize-none border-0 bg-transparent text-sm',
                'placeholder:text-muted-foreground',
                'focus:outline-none focus:ring-0',
                'min-h-[24px] max-h-[120px]',
                'scrollbar-thin'
              )}
            />
          </div>

          {/*  */}
          <SendButton
            canSend={canSend}
            isLoading={disabled}
            onClick={handleSend}
          />
        </div>

        {/*  */}
        <motion.div
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{ delay: 0.1 }}
          className="flex items-center justify-between mt-3 px-1"
        >
          <div className="text-xs text-muted-foreground">
            {files && files.length > 0 ? (
              <span className="flex items-center gap-1">
                <ImageIcon className="h-3 w-3" />
                 {files.length} 
              </span>
            ) : (
              <span></span>
            )}
          </div>
          
          <div className="text-xs text-muted-foreground flex items-center gap-2">
            <kbd className="px-1.5 py-0.5 bg-muted rounded text-xs">Shift</kbd>
            <span>+</span>
            <kbd className="px-1.5 py-0.5 bg-muted rounded text-xs">Enter</kbd>
            <span></span>
          </div>
        </motion.div>
      </div>
    </motion.div>
  );
}
